import "core-js/modules/es.array.push.js";
import Config from '@/config';
import { getToken } from '@/lin/util/token';
import axios from 'axios';
import { marked } from 'marked';
import OrderPayDialog from '@/view/order/order-pay-dialog';
import { ElMessage } from 'element-plus';
export default {
  components: {
    OrderPayDialog
  },
  data() {
    return {
      messages: [{
        role: 'system',
        content: '你好，我是邻智AI测算助手，提供八字合婚,流年运势,宝宝/公司起名,姓名配对,八字精批,塔罗牌占卜等在线测算，我会根据您提供的信息进行详细的计算和分析，有什么需要我的帮助吗？'
      }],
      inputMessage: '',
      wdMessages: {},
      isLoading: false,
      // 添加一个标志变量
      typeWriterTimer: null,
      publishDialogVisible: false,
      dialogTitle: '',
      ai_chat_count: this.$store.state.user.ai_chat_count ? this.$store.state.user.ai_chat_count : null
    };
  },
  methods: {
    async sendMessage() {
      if (this.inputMessage.trim() === '') return;
      const str = this.inputMessage;
      this.inputMessage = '';
      this.messages.push({
        role: 'user',
        content: str
      });
      this.scrollToBottom();

      // 设置加载状态
      this.isLoading = true;
      await this.getAiResponse();

      // 取消加载状态
      this.isLoading = false;
      this.scrollToBottom();
    },
    async showPay() {
      this.$router.push('/server');
    },
    async getAiResponse() {
      try {
        const accessToken = getToken('access_token');
        const response = await axios.post(Config.baseURL + `api/blog/articles/chatstream`, this.messages, {
          headers: {
            'Content-Type': 'application/json; charset=utf-8',
            'Authorization': accessToken
          },
          responseType: 'text'
        });
        const lines = response.data.split('\n');
        let fullResponse = '';
        this.messages.push({
          role: 'system',
          content: ''
        });
        for (const line of lines) {
          if (line.trim() === '' || line === 'data: [DONE]') continue;
          const trimmedLine = line.startsWith('data: ') ? line.substring(6) : line;
          try {
            const data = JSON.parse(trimmedLine);
            if (data.choices && data.choices[0] && data.choices[0].delta && data.choices[0].delta.content) {
              const newContent = data.choices[0].delta.content;
              fullResponse += newContent;
            }
          } catch (error) {
            console.error('Error parsing line:', line, error);
          }
        }
        this.typeWriterEffect(fullResponse);
      } catch (error) {
        this.messages.push({
          role: 'system',
          content: '系统繁忙，请输入继续！'
        });
        this.inputMessage = '继续';
      } finally {
        // 确保在请求完成或失败后取消加载状态
        this.isLoading = false;
      }
    },
    async SendwdMessages() {
      try {
        this.wdMessages.title = this.dialogTitle;
        this.wdMessages.messages = this.messages;
        const accessToken = getToken('access_token');
        const response = await axios.post(Config.baseURL + `api/blog/articles/wdarticle`, this.wdMessages, {
          headers: {
            'Content-Type': 'application/json; charset=utf-8',
            'Authorization': accessToken
          },
          responseType: 'text'
        });
        //返回true和false 展示发布成功和失败
        if (response.data === true) {
          ElMessage.success('发布成功');
        } else {
          let msg = '此功能是主播专属功能，请开通主播卡！<a href="/#/server" style="color: #409EFF; text-decoration: underline;">点此订阅</a>';
          ElMessage.warning({
            message: msg,
            dangerouslyUseHTMLString: true,
            duration: 6000
          });
        }
        this.publishDialogVisible = false;
      } catch (error) {
        this.messages.push({
          role: 'system',
          content: '系统异常，请联系管理员！'
        });
        ElMessage.error('发布失败,系统异常');
      } finally {
        // 确保在请求完成或失败后取消加载状态
        this.isLoading = false;
        this.publishDialogVisible = false;
      }
    },
    typeWriterEffect(content) {
      let index = 0;
      // 找到最后一个 role 为 'system' 的消息
      const lastSystemMessage = this.messages.slice().reverse().find(message => message.role === 'system');

      // 如果没有找到 system 消息，直接返回
      if (!lastSystemMessage) return;

      // 清除之前的定时器
      if (this.typeWriterTimer) {
        clearInterval(this.typeWriterTimer);
      }

      // 每50ms更新一次内容，逐字符显示
      this.typeWriterTimer = setInterval(() => {
        if (index < content.length) {
          lastSystemMessage.content += content[index];
          index++;
          this.scrollToBottom();
        } else {
          clearInterval(this.typeWriterTimer); // 完成后停止定时器
          this.scrollToBottom();
        }
      }, 20);
      this.scrollToBottom();
    },
    parseMarkdown(content) {
      return marked.parse(content);
    },
    scrollToBottom() {
      setTimeout(() => {
        const chatBody = this.$refs.chatBody;
        chatBody.scrollTop = chatBody.scrollHeight;
      }, 100);
    },
    showPublishDialog() {
      this.dialogTitle = '';
      this.publishDialogVisible = true;
    },
    publishChat() {
      if (!this.dialogTitle.trim()) {
        ElMessage.warning('请输入标题');
        return;
      }
      const now = new Date();
      const dateStr = `${now.getFullYear()}-${String(now.getMonth() + 1).padStart(2, '0')}-${String(now.getDate()).padStart(2, '0')}`;
      this.dialogTitle = this.dialogTitle + ' ' + dateStr;
      this.SendwdMessages();
    }
  },
  mounted() {
    // 在组件加载时检查 URL 参数
    const userInput = this.$route.query.user_input;
    const dataId = this.$route.query.data_id;
    if (dataId) {
      // 如果存在 data_id 参数，从localStorage获取数据
      import('@/lin/util/data-transfer').then(({
        getData,
        removeData
      }) => {
        const data = getData(dataId);
        if (data) {
          // 构建消息内容
          let content = data;

          // 将构建的内容添加到消息列表
          this.messages.push({
            role: 'user',
            content: content
          });
          this.isLoading = true;
          this.getAiResponse();
          this.scrollToBottom();

          // 使用完数据后从localStorage中删除
          removeData(dataId);
        } else {
          ElMessage.warning('数据已过期或不存在');
        }
      }).catch(error => {
        console.error('读取数据失败:', error);
        ElMessage.error('读取数据失败');
      });
    } else if (userInput) {
      // 如果存在 user_input 参数，将其作为用户输入消息
      this.messages.push({
        role: 'user',
        content: userInput
      });
      this.isLoading = true;
      this.getAiResponse();
      this.scrollToBottom();
    }
  },
  computed: {
    loggedIn() {
      return this.$store.state.loggedIn;
    }
  }
};